.form button {
  background: red;
  border: none;
  outline: none;
  color: white;
  font-size: 20px;
  padding-inline: 25px;
  padding-block: 10px;
  border: 1px solid red;
  cursor: pointer;
  text-transform: uppercase;
}

html::-webkit-scrollbar {
  display: none;
}

html::-webkit-scrollbar-thumb {
  display: none;
}

.form button:hover {
  background-color: transparent;
  color: white;
  transition: 285ms ease-out;
}

.form div {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.form input {
  border: 1px solid red;
  border-right: none;
  outline: none;
  color: white;
  font-size: 20px;
  padding-inline: 8px;
  padding-block: 10px;
  width: 400px;
  background-color: transparent;
}
.form ::placeholder {
  color: white;
}

