    width: 100%;
    border-radius: 0px;
    box-shadow: none;
  }
  .sp-inf {
    width: 100%;
  }
}
body {
  margin: 0px;
  padding: 0px;
}

.wrapper-login {
  height: 100vh;
  background-color: #f7f7f7;
}

.form-acc {
  padding-inline: 20px;
  padding-block: 35px;
  width: 450px;
  background-color: white;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.34) 0px 4px 9px;
}

.logoimg {
  width: 120px;
  height: auto;
  margin-block: 15px;
}

.create-headline {
  color: red;
  font-size: 37px;
  font-family: "Anton", sans-serif;
  margin-bottom: 25px;
  text-shadow: 1px 1px 1px black;
}

.input-field {
  margin-block: 13px;
  border: 1px solid red;
  outline: none;
  width: 87%;
  padding-block: 10px;
  font-size: 19px;
  padding-inline: 10px;
  color: darkgray;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.34) 0px 1px 2px;
}

.input-field:focus {
  box-shadow: rgba(0, 0, 0, 0.34) 0px 3px 6px;
  transition: 200ms ease-out;
}

.btn-create-account {
  border: 1px solid red;
  color: white;
  font-size: 20px;
  width: 87%;
  padding-block: 8px;
  background-color: red;
  margin-block: 10px;
  border-radius: 5px;
}

.btn-create-account:hover {
  background-color: white;
  color: red;
  transition: 285ms ease-out;
}

.link-to-acc {
  color: red;
  text-decoration: underline;
}

.btn-create-account:active {
  animation: clickedBtn 250ms ease-out;
}

@keyframes clickedBtn {
  0% {
    transform: scale(0.9);
    box-shadow: rgba(0, 0, 0, 0.34) 0px 1px 2px;
  }
  50% {
    transform: scale(1.1);
    box-shadow: rgba(0, 0, 0, 0.34) 0px 5px 10px;
  }
  100% {
    transform: scale(1);
    box-shadow: rgba(0, 0, 0, 0.34) 0px 1px 2px;
  }
}
